.search-form {

	position: relative;
	// .search-form__input

	&__input {
		@extend %text-12;
		line-height: 1.4rem;
		//color: #101112;
		background-color: #fff;
		border: 0.1rem solid #777;
		width: 33.6rem;
		padding: 1.1rem 1.5rem 1.1rem 3.5rem;
		background-image: url(../images/icons/search.svg);
		background-size: 1.6rem 1.6rem;
		background-position: 1rem 50%;
		background-repeat: no-repeat;
		transition: .5s width;

		&::placeholder {
			font-style: italic;
			color: #9b9b9b;
			//color: #777;
		}

		@media(max-width: 1024px) {
			border-color: #2D703C;
			padding: 1rem 2.7rem 1rem 3.4rem;
		}

		&--active {
			width: 100%;
		}

		&::placeholder {
			color: #777777;
		}
	}

	&__help {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		z-index: 25;
		padding: 1.6rem;
	}
}

.help-search {
	display: none;
	background-color: #FFF;
	box-shadow: 0rem -0.1rem 1rem rgba(0, 0, 0, 0.1);

	&__item {
		display: block;
		@extend %text-12;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}

		span {
			color: #777;
		}

		@media (any-hover: hover) {
			&:hover {
				color: $green;

				span {
					color: $green;
				}
			}
		}
	}
}
